import React, { Component, useState } from 'react';

import CandleStickChart from "./CandleStickChart/index";
import LineChart from "./LineChart/LineChart";

export default function App(){
  const [ line, setLine ] = useState(false);


  function onSwitch(){
    setLine(!line);
  }

  return (
    <div className="App">
      <button onClick={onSwitch}>Switch type chart</button>
      {
        ! line && 
        <CandleStickChart />
      }
      {
        line && 
        <LineChart />
      }
    </div>
  )
}
